@import "../../../constant/constant";
$size: calc(100% / 5);
.container {
  width: $size;
  height: $size;
  position: relative;
  .information {
    position: absolute;
    padding-top: 5px;
    z-index: 100;
    width: $size;
    height: $size;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 50%; /* Move B 50% down from the top of A */
    left: 50%; /* Move B 50% from the left of A */
    transform: translate(-50%, -50%);
    text-shadow: 1px 0 #000, -1px 0 #000, 0 1px #000, 0 -1px #000, 1px 1px #000,
      -1px -1px #000, 1px -1px #000, -1px 1px #000;
    .num {
      font-size: $fontSizeBig;
      color: #fff;
    }
    .type {
      font-size: $fontSizeTiny;
      color: #fff;
    }
  }
}
