.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  box-sizing: border-box;
  .content {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
    .header {
      justify-content: center;
      flex: 3;
      display: flex;
      align-items: flex-start;
    }
    .totalDate {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex: 2;
    }
    .startDate {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex: 1;
    }
    .image {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 2;
    }
    .counterList {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex: 2;
    }
  }
}
