body,
input,
html {
  margin: 0;
  padding: 0;
  height: 100%; /* Ensure body and html fill the viewport height */
  font-family: "Dancing Script";
  font-size: 20px;
}

/* Style the body with dimensions smaller than 100vh and 100vw */
body {
  background-color: #fffafc; /* Example background color */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Style the root div to occupy full viewport width and height */
#root {
  width: 100vw; /* 100% of the viewport width */
  height: 100vh; /* 100% of the viewport height */
  background-color: #fffafc; /* White background for root div */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Additional styles for content within the root div */
.root-content {
  text-align: center;
  padding: 20px;
}
