@import "constant/constant.scss";

.container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .content {
    width: 90%;
    height: 80%;
    max-width: 450px;
    max-height: 500px;
    background-color: #fcc2c2;
    border-radius: 20px;
    background: rgba(255, 199, 199, 0.7);
    padding: 20px;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    .header {
      height: 150px;
      width: 150px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .button {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 10px;
      width: 100%;
      background-color: $heart;
      margin: 10px;
      box-sizing: border-box;
      border-radius: 10px;
      color: white;
    }
  }
}
