@import "constant/constant.scss";

.container {
  position: relative;
  .content {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    .cuteRevert {
      flex: 3;
      padding-bottom: 30px;
      transform: scaleX(-1);
    }
    .label {
      flex: 4;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px 20px;
      font-size: $fontSizeLargest;
    }
    .cute {
      flex: 3;
      padding-bottom: 30px;
    }
  }
}
