.container {
  width: 100%;
  height: 100%;
  padding: 50px 10px 10px 10px;
  box-sizing: border-box;
  .menu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    .item {
      border: 1px solid #ff9292;
      background-color: #ffdede;
      display: flex;
      flex-direction: column;
      margin: 5px;
      border-radius: 10px;
      justify-content: center;
      align-items: center;
      width: calc(100% * (1 / 3) - 10px - 1px);
      aspect-ratio: 1;
      box-sizing: border-box;
      .label {
        padding: 5px;
      }
      .icon {
        width: 100%;
        height: 100%;
        padding: 20px;
        box-sizing: border-box;
      }
    }
  }
}
