.container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  .content {
    margin-top: 50px;
    margin-bottom: 50px;
    width: 100%;
    position: relative;
    box-sizing: border-box;
    .imageLedLine {
      width: 100%;
      object-fit: contain;
    }
    .revert {
      transform: scaleX(-1);
    }
  }
  .animation {
    position: absolute;
    z-index: 100;
    width: 100px;
    height: 100px;
    bottom: 0px;
  }
  .animationRevert {
    right: 0px;
  }
}
