.container {
  animation: shake 3s;
  animation-iteration-count: infinite;
  .light {
    position: absolute;
    top: 3px;
    width: 25px;
    height: 25px;
    transform: translate(-50%, -50%) scaleY(-1);
    left: 50%;
    .image {
      width: 25px;
      height: 25px;
    }
  }
}

@keyframes shake {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-2deg);
  }
  50% {
    transform: rotate(2deg);
  }
  75% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
