@import "constant/constant.scss";
.container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  .animation2 {
    position: absolute;
    z-index: 100;
    width: 100px;
    height: 100px;
    bottom: 0px;
    right: 50px;
  }
  .info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .title {
      font-size: $fontSizeBig;
    }
    .num {
      flex-direction: row;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      font-size: 68px;
      margin-top: 10px;
    }
    .sub {
      font-size: $fontSizeNormal;
      padding-bottom: 10px;
    }
  }
}
